export const TRY_CALL = 'p2pcall/TRY_CALL';
export const tryCall = (subscriber) => {
  return {
    type: TRY_CALL,
    payload: subscriber,
  };
};

export const CALL_RECIEVED = 'p2p/CALL_RECIEVED';
export const callRecieved = (from) => {
  return {
    type: CALL_RECIEVED,
    payload: from,
  };
};

export const CALL_INTERRUPTED = 'p2p/CALL_INTERRUPTED';
export const callInterupted = () => {
  return {
    type: CALL_INTERRUPTED,
  };
};
