import { handleActions } from 'redux-actions';
import { RECIEVE_USER_DATA, CLEAR_USER_DATA } from '../actions/user';

const userReducer = handleActions(
  {
    [RECIEVE_USER_DATA]: (state, action) => ({
      ...state,
      userData: action.payload,
      errorText: '',
    }),
    [CLEAR_USER_DATA]: (state, action) => ({
      ...state,
      userData: '',
    }),
  },
  { userData: '', errorText: '' }
);

export default userReducer;
