import { handleActions } from 'redux-actions';
import { TRY_CALL, CALL_RECIEVED, CALL_INTERRUPTED } from '../actions/p2p';

const p2preducer = handleActions(
  {
    [TRY_CALL]: (state, action) => ({
      ...state,
      tryCall: true,
      subscriber: action.payload,
    }),

    [CALL_RECIEVED]: (state, action) => ({
      ...state,
      callFrom: action.payload,
    }),

    [CALL_INTERRUPTED]: (state, action) => ({
      ...state,
      tryCall: false,
      callFrom: null,
      subscriber: null,
    }),
  },
  { tryCall: false, subscriber: null, callFrom: null }
);

export default p2preducer;
