import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import serverConfig from '../server-config';
import Emoji from '../shared/Emoji';

function ReconfirmEmail({ email, token, userId }) {
  const sendConfirmationEmail = async () => {
    if (!token) return;
    const response = await fetch(`${serverConfig.baseUrl}/users/email/confirm`, {
      method: 'post',
      body: JSON.stringify({ redirectUrl: `${window.location.origin}/#/profile/${userId}` }),
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json',
      },
    });
  };

  useEffect(() => {
    sendConfirmationEmail();
  }, [userId]);

  return email ? (
    <div
      style={{ height: '90vh', display: 'flex', textAlign: 'center', justifyContent: 'center', alignItems: 'center' }}
    >
      <h1 style={{ fontSize: '4rem', margin: '40px' }}>
        Confirmation email has been sent to <br />
        <Emoji label="email">ðŸ’Œ</Emoji>
        <span style={{ color: 'deeppink' }}>{email}</span>
        <Emoji label="email">ðŸ’Œ</Emoji>
      </h1>
    </div>
  ) : (
    <p>loading...</p>
  );
}

export default connect((state) => ({
  email: state.user.userData.email,
  token: state.auth.token,
  userId: state.user.userData._id,
}))(ReconfirmEmail);
