import serverConfig from '../server-config';
import { fetchUserData, clearUserData } from './user';

export const RECIEVE_TOKENS = 'auth/RECIEVE_TOKENS';
export const recieveTokens = ({ token, userId }) => {
  localStorage.setItem('signedIn', 'true');

  return {
    type: RECIEVE_TOKENS,
    payload: { token, userId },
  };
};

export const SET_ERROR_TEXT = 'auth/SET_ERROR_TEXT';
export const setErrorText = (err) => {
  return {
    type: SET_ERROR_TEXT,
    payload: { errorText: err },
  };
};

export const CLEAR_AUTH_DATA = 'auth/CLEAR_AUTH_DATA';
export const clearAuthData = () => {
  return {
    type: CLEAR_AUTH_DATA,
  };
};

export const refreshAccessToken = () => async (dispatch) => {
  const response = await fetch(`${serverConfig.baseUrl}/token/refresh`, {
    method: 'get',
    headers: { 'Content-Type': 'application/json', Accept: 'application/json' },
    credentials: 'include',
  });

  if (response.ok) {
    const json = await response.json();

    dispatch(recieveTokens(json));
    dispatch(fetchUserData(json.userId));
    localStorage.setItem('signedIn', 'true');
  } else {
    localStorage.setItem('signedIn', 'false');
  }
};

export const requestLogin = (email, password) => async (dispatch) => {
  const response = await fetch(`${serverConfig.baseUrl}/auth/login`, {
    method: 'post',
    headers: { 'Content-Type': 'application/json', Accept: 'application/json' },
    credentials: 'include',
    body: JSON.stringify({ email, password }),
  });

  if (response.ok) {
    localStorage.setItem('signedIn', 'true');

    const json = await response.json();
    dispatch(recieveTokens(json));
    dispatch(fetchUserData(json.userId));

    window.location.href = '/#/';
  } else {
    localStorage.setItem('signedIn', 'false');
    dispatch(setErrorText('Incorrect email or password'));
  }
};

export const signOut = () => async (dispatch) => {
  await fetch(`${serverConfig.baseUrl}/auth/logout`, { credentials: 'include' });
  localStorage.setItem('signedIn', 'false');

  dispatch(clearAuthData());
  dispatch(clearUserData());
};
