import serverConfig from '../server-config';

export const RECIEVE_USER_DATA = 'users/RECIEVE_USER_DATA';
export const recieveUserData = (data) => {
  return {
    type: RECIEVE_USER_DATA,
    payload: data,
  };
};

export const CLEAR_USER_DATA = 'users/CLEAR_USER_DATA';
export const clearUserData = () => {
  return {
    type: CLEAR_USER_DATA,
  };
};

export const fetchUserData = (userId) => async (dispatch) => {
  const response = await fetch(`${serverConfig.baseUrl}/users/get/by/id/${userId}`, {
    headers: { 'Content-Type': 'application/json', Accept: 'application/json' },
  });
  const data = await response.json();

  dispatch(recieveUserData(data));
};

export const uploadPhoto = (userId, data, jwt) => async (dispatch) => {
  await fetch(`${serverConfig.baseUrl}/users/update/image/${userId}`, {
    method: 'put',
    body: data,
    headers: {
      Authorization: `Bearer ${jwt}`,
    },
  });

  dispatch(fetchUserData(userId));
};
