import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { ThemeContext } from '../../core/Page/Page';
import colors from '../../colors';

import './Button.css';

function Button(props) {
  const { theme } = useContext(ThemeContext);

  const style = {
    background: theme === 'dark' ? 'rgb(23 29 45)' : colors.common.primary,
    boxShadow: `2px 2px 3px ${theme === 'dark' ? '#000' : '#888'}`,
  };

  return (
    <button {...props} type={props.type} style={{ ...style, ...props.style }} className="app-button">
      {props.children}
    </button>
  );
}

Button.propTypes = {
  type: PropTypes.oneOf(['button', 'submit']),
};

Button.defaultProps = {
  type: 'button',
};

export default Button;
