import React, { useContext, useState } from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import { ThemeContext } from '../Page/Page';
import Button from '../../shared/Button/Button';
import styled, { keyframes } from 'styled-components';
import ThemedField from '../../shared/ThemedField/ThemedField';
import colors from '../../colors';
import serverConfig from '../../server-config';

import './RegisterForm.css';

export const FormTitle = styled.h2`
  font-size: 2rem;
  margin-bottom: 20px;
`;

const spinning = keyframes`
  0% { transform: rotate(0deg) }
  100% { transform: rotate(360deg) }
`;

const FieldSpinner = styled.div`
  position: absolute;
  right: 10px;
  top: 43px;
  width: 20px;
  height: 20px;
  border: 3px solid ${colors.common.primary};
  border-color: ${colors.common.primary} ${colors.common.primary} ${colors.common.primary} transparent;
  border-radius: 50%;
  animation: ${spinning} 1s infinite linear;
`;

// const FormImage = styled.div`
//   position: absolute;
//   width: 300px;
//   heigth: 50px;
//   top: -200px;
// `;

function RegisterForm(props) {
  const { theme } = useContext(ThemeContext);
  const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
  const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[A-Za-z\d]{8,30}$/;
  const [emailError, setEmailError] = useState('');
  const [emailChecking, setEmailChecking] = useState(false);
  const [emailDirty, setEmailDirty] = useState(false);

  const myHandleBlur = async (e, handleBlur) => {
    handleBlur(e);
    setEmailDirty(true);
    const email = e.target.value;

    try {
      if (email && emailRegex.test(email)) {
        setEmailChecking(true);

        const res = await fetch(`${serverConfig.baseUrl}/email-check/${email}`);
        const error = await res.text();

        if (res.status !== 200) {
          setEmailError(error);
        } else {
          setEmailError('');
        }

        setEmailChecking(false);
      }
    } catch {}
  };

  const getEmailErrors = (emailCheckEr, emailValidationEr) => {
    if (emailDirty) {
      return emailCheckEr || emailValidationEr;
    }
  };

  return (
    <div className="app-register" style={{}}>
      <Formik
        initialValues={{ email: '', fullname: '', password: '', repeat_password: '' }}
        validate={(values) => {
          const errors = {};

          if (!values.email) {
            errors.email = 'Email is required';
            setEmailError('');
          } else if (!emailRegex.test(values.email)) {
            errors.email = 'Invalid email address';
            setEmailError('');
          }

          if (!values.fullname) {
            errors.fullname = 'Fullname is required';
          }

          if (!values.password) {
            errors.password = 'Password is required';
          } else if (!passwordRegex.test(values.password)) {
            errors.password =
              'Password must contains from 8 to 30 characters and \nmin 1 uppercase letter\nmin 1 lowercase letter\nmin 1 number\n';
          }

          if (values.password !== values.repeat_password) {
            errors.repeat_password = 'Passwords are not same';
          }

          return errors;
        }}
        onSubmit={async (values, { setSubmitting }) => {
          props.onSubmit(values);
        }}
      >
        {({ isSubmitting, handleBlur, errors }) => (
          <Form className={`app-form app-form-${theme}`}>
            <FormTitle>
              <span aria-label="party-face-emoji" role="img">
                ðŸ¥³
              </span>
              Join us!
              <span aria-label="party-face-emoji" role="img">
                ðŸ¥³
              </span>
            </FormTitle>

            <ThemedField>
              <label>Your fullname</label>
              <Field autoFocus={true} placeholder="David Bowie" type="text" name="fullname" />
              <ErrorMessage name="fullname" component="span" />
            </ThemedField>

            <ThemedField>
              {emailChecking && <FieldSpinner />}

              <label>Email (we will send you a confrimation link)</label>
              <Field onBlur={(e) => myHandleBlur(e, handleBlur)} placeholder="abc@gmail.com" type="text" name="email" />
              <span>{getEmailErrors(emailError, errors.email)}</span>
            </ThemedField>

            <ThemedField>
              <label>Password</label>
              <Field type="password" name="password" placeholder="Do not forget it :)" />
              <ErrorMessage name="password" component="span" />
            </ThemedField>

            <ThemedField>
              <label>Repeat password</label>
              <Field type="password" name="repeat_password" placeholder="" />
              <ErrorMessage name="repeat_password" component="span" />
            </ThemedField>

            <Button type="submit">Sign up!</Button>
          </Form>
        )}
      </Formik>
    </div>
  );
}

export default RegisterForm;
