import React, { useContext, useState, useRef, useEffect } from 'react';
import styled from 'styled-components';
import { ThemeContext } from '../../../Page/Page';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPaperPlane, faCog, faPhoneAlt } from '@fortawesome/free-solid-svg-icons';
import colors from '../../../../colors';
import { Link } from 'react-router-dom';
import serverConfig from '../../../../server-config';

import './TeamThread.css';

const InfoMessage = styled.div`
  text-alight: center;
  margin: 0 auto;
  padding: 5px 10px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.03);
`;

const YourMessage = styled.div`
  background: #0a0d14;
  border-radius: 20px;
  color: #fff;
  padding: 15px 20px;
  margin: 10px 0;
  position: relative;

  &::after {
    content: '';
    display: block;
    position: absolute;

    right: -5px;
    bottom: 0;

    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 20px 20px 20px;
    border-color: transparent transparent #0a0d14 transparent;
  }
`;

const AnothersMessage = styled.div`
  background: ${colors.common.primary};
  color: #fff;
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  color: #fff;
  padding: 15px 20px;
  margin: 10px 0;
  position: relative;
  max-width: 80%;
  margin-left: 20px;

  &::after {
    content: '';
    display: block;
    position: absolute;

    left: -13px;
    top: 0;

    width: 0;
    height: 0;
    border-style: solid;
    border-width: 20px 20px 0px 20px;
    border-color: ${colors.common.primary} transparent transparent transparent;
  }
`;

const Teammate = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;

  & > .teammate-img {
    width: 40px;
    height: 40px;
    background-size: cover;
    background-position: center;
    overflow: hidden;
    border-radius: 50%;
    margin-right: 10px;
  }
`;

function TeamPage(props) {
  const { teamInfo, user } = props;
  const { theme } = useContext(ThemeContext);
  const [msgInputValue, setMsgInputValue] = useState('');
  const { socket } = props;
  const messagesList = useRef();
  const [messages, setMessages] = useState(teamInfo.conversation);

  useEffect(() => {
    messagesList.current.scrollTop = messagesList.current.scrollHeight;
  }, []);

  useEffect(() => {
    if (socket) {
      socket.on('message recieved', (msg) => {
        console.log('here msg recieved');
        const newMessages = messages;
        newMessages.push(msg);
        setMessages([]);
        setMessages(newMessages);

        if (messagesList.current) {
          messagesList.current.scrollTop = messagesList.current.scrollHeight;
        }
      });
    }
  }, [socket]);

  const getMsgType = (msg) => {
    if (!msg.userId) {
      return 'info';
    } else if (msg.userId === user._id) {
      return 'your';
    } else {
      return 'other';
    }
  };

  const handleMsgInputChange = (e) => {
    setMsgInputValue(e.target.value);
  };

  const handleTryP2PCall = (whom) => {
    props.tryCall({ whom, teamId: teamInfo._id });
  };

  const handleMsgSend = (e) => {
    e.preventDefault();

    if (msgInputValue.trim()) {
      console.log('here msg send');
      socket.emit('message send', { message: msgInputValue.trim(), userId: user._id, teamId: teamInfo._id });
      setMsgInputValue('');
    }
  };

  const displayMessages = () => {
    return messages.map((msg) => {
      const type = getMsgType(msg);

      if (type === 'info') {
        return (
          <li style={{ alignSelf: 'center' }} key={msg._id}>
            <InfoMessage>{msg.message}</InfoMessage>
          </li>
        );
      } else if (type === 'your') {
        return (
          <li style={{ alignSelf: 'flex-end' }} key={msg._id}>
            <YourMessage>{msg.message}</YourMessage>
          </li>
        );
      } else {
        const userInfo = teamInfo.teammates.find((t) => t._id === msg.userId);

        return (
          <li style={{ display: 'flex', alignSelf: 'flex-start' }} key={msg._id}>
            <div
              className="user-msg-img"
              style={{ backgroundImage: `url(${serverConfig.baseUrl}/${userInfo?.imageSrc})` }}
            >
              {!userInfo?.imageSrc && (userInfo?.name.substr(0, 2) || <span>X</span>)}
            </div>

            <AnothersMessage>
              <span className="user-msg-name">{userInfo?.name || 'Not a teammate anymore'}</span>
              <span className="user-msg-text">{msg.message}</span>
            </AnothersMessage>
          </li>
        );
      }
    });
  };

  const displayTeammates = () => {
    return teamInfo.teammates.map((teammate) => {
      return (
        <li style={{ margin: '10px 0' }} key={teammate._id}>
          <Teammate className="teammate">
            <div
              className="teammate-img"
              style={{ backgroundImage: `url(${serverConfig.baseUrl}/${teammate.imageSrc})` }}
            >
              {!teammate.imageSrc && teammate.name.substr(0, 2)}
            </div>
            <Link style={{ color: colors[theme].foreground }} to={`/profile/${teammate._id}`}>
              {teammate.name}
            </Link>
            {user._id !== teammate._id && (
              <span onClick={() => handleTryP2PCall(teammate)} className="try-call-btn">
                <FontAwesomeIcon icon={faPhoneAlt} /> Call
              </span>
            )}
          </Teammate>
        </li>
      );
    });
  };

  return (
    <div className="team-page-main-section">
      <div className={`messages-container messages-container-${theme}`}>
        <ul ref={messagesList} className="messages-list">
          {displayMessages()}
        </ul>
        <div className={`messages-input messages-input-${theme}`}>
          <form onSubmit={handleMsgSend}>
            <input
              autoFocus={true}
              type="text"
              value={msgInputValue}
              placeholder="Type your message here..."
              onChange={handleMsgInputChange}
            />
            <button className="send-btn">
              <FontAwesomeIcon style={{ transform: 'rotate(45deg) translateY(-2px)' }} icon={faPaperPlane} />
            </button>
          </form>
        </div>
      </div>
      {/*  */}
      <div className="team-about-section">
        <h1 className="team-name">
          {teamInfo.name}
          {teamInfo.ownerId === user._id && (
            <Link
              style={{ color: colors[theme].foreground }}
              to={`/teams/thread/${teamInfo._id}/settings`}
              className="team-settings-btn"
            >
              <FontAwesomeIcon icon={faCog} />
            </Link>
          )}
        </h1>
        <ul className="teammates">{displayTeammates()}</ul>
      </div>
    </div>
  );
}

export default TeamPage;
