import { handleActions } from 'redux-actions';
import { RECIEVE_TOKENS, SET_ERROR_TEXT, CLEAR_AUTH_DATA } from '../actions/auth';

const authReducer = handleActions(
  {
    [RECIEVE_TOKENS]: (state, action) => ({
      ...state,
      token: action.payload.token,
      userId: action.payload.userId,
      errorText: '',
      signedIn: 'true',
    }),
    [SET_ERROR_TEXT]: (state, action) => ({
      ...state,
      errorText: action.payload.errorText,
    }),
    [CLEAR_AUTH_DATA]: (state, action) => ({
      ...state,
      userId: '',
      token: '',
      signedIn: 'false',
    }),
  },
  { userId: '', token: '', errorText: '', signedIn: localStorage.getItem('signedIn') || 'false' }
);

export default authReducer;
