import React from 'react';
import CreateTeamForm from './CreateTeamForm';
import serverConfig from '../../server-config';
import { connect } from 'react-redux';

function CreateTeam(props) {
  const handleSubmit = async (form) => {
    const creationResponse = await fetch(`${serverConfig.baseUrl}/teams/create`, {
      method: 'post',
      headers: {
        Authorization: `Bearer ${props.jwt}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        name: form.name,
        description: form.description || '',
        technologies: form.technologies || [],
      }),
    });

    const { _id } = await creationResponse.json();

    form.imageFormData && await fetch(`${serverConfig.baseUrl}/teams/upload/image/${_id}`, {
      headers: {
        Authorization: `Bearer ${props.jwt}`,
      },
      method: 'post',
      body: form.imageFormData,
    });

    window.location.href = "/#/teams/thread/" + _id;
  };

  return <CreateTeamForm onSubmit={handleSubmit} />;
}

export default connect((state) => ({ jwt: state.auth.token }))(CreateTeam);
