import React, { useContext } from 'react';
import { Formik, Form, ErrorMessage, Field } from 'formik';
import ThemedField from '../../shared/ThemedField/ThemedField';
import { FormTitle } from '../Register/RegisterForm';
import { ThemeContext } from '../Page/Page';
import Button from '../../shared/Button/Button';
import { Link } from 'react-router-dom';

import './LoginForm.css';

function Login(props) {
  const { theme } = useContext(ThemeContext);
  const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;

  return (
    <div className="app-login">
      <Formik
        initialValues={{ email: '', password: '' }}
        validate={(values) => {
          const errors = {};

          if (!values.email) {
            errors.email = 'Email is required';
          } else if (!emailRegex.test(values.email)) {
            errors.email = 'Invalid email address';
          }

          if (!values.password) {
            errors.password = 'Password is required';
          }

          return errors;
        }}
        onSubmit={(values) => props.onSubmit(values)}
      >
        {() => (
          <Form className={`app-form app-form-${theme}`}>
            <FormTitle>
              Login to your account!
              <span aria-label="sipped-face-emoji" role="img">
                ðŸ¤
              </span>
            </FormTitle>

            <ThemedField>
              <label>Your email</label>
              <Field autoFocus={true} placeholder="abc@gmail.com" type="text" name="email" />
              <ErrorMessage name="email" component="span" />
            </ThemedField>

            <ThemedField>
              <label>Your password</label>
              <Field type="password" name="password" />
              <ErrorMessage name="password" component="span" />
            </ThemedField>

            {props.children}

            <Button type="submit">Login</Button>
          </Form>
        )}
      </Formik>
    </div>
  );
}

export default Login;
