import React, { useRef, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import io from 'socket.io-client';
import Peer from 'simple-peer';
import serverConfig from '../../server-config';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMicrophone, faMicrophoneSlash, faVideo, faPhone, faVideoSlash } from '@fortawesome/free-solid-svg-icons';
import { callRecieved, callInterupted } from '../../actions/p2p';

import './Call.css';

const iceConfig = {
  iceServers: [
    {
      urls: 'stun:numb.viagenie.ca',
      username: 'sultan1640@gmail.com',
      credential: '98376683',
    },
    {
      urls: 'turn:numb.viagenie.ca',
      username: 'sultan1640@gmail.com',
      credential: '98376683',
    },
  ],
};

function Call(props) {
  const { user, p2p } = props;
  const { socket } = props;
  const localStream = useRef();
  const ringtoneAudio = useRef();
  const localVideo = useRef();
  const partnerVideo = useRef();
  const yourPeer = useRef();
  const anotherPeer = useRef();
  const [subscriber, setSubscriber] = useState(false);
  const [calling, setCalling] = useState(false);
  const [incomingCall, setIncomingCall] = useState(false);
  const [localAudioEnabled, setLocalAudioEnabled] = useState(true);
  const [localVideoEnabled, setLocalVideoEnabled] = useState(true);
  const [teamId, setTeamId] = useState(null);

  const callerData = useRef();

  let callEstablished = false;

  const fetchStreamFromMediaDevices = async (video) => {
    let stream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true });
    setLocalVideoEnabled(video);

    stream.getVideoTracks()[0].enabled = video;

    localVideo.current.srcObject = stream;
    localStream.current = stream;
    // localStream.current.getVideoTracks()[0].enabled = video;
  };

  useEffect(() => {
    if (!socket || !user) return;

    socket.on('you have a call', (data) => {
      if (data.to.userId !== user._id) {
        return;
      }

      setTeamId(data.to.teamId);
      props.dispatch(callRecieved(data.from));
      callerData.current = data;
    });
  }, [socket, user]);

  const acceptCall = async (video) => {
    ringtoneAudio.current.muted = true;
    ringtoneAudio.current.src = '';
    setIncomingCall(false);

    await fetchStreamFromMediaDevices(video);
    setCalling(true);
    const stream = localStream.current;

    const data = callerData.current;

    anotherPeer.current = new Peer({
      initiator: false,
      trickle: false,
      stream,
      config: iceConfig,
    });

    anotherPeer.current.on('signal', (answer) => {
      socket.emit('accept call', {
        signalFromPartner: answer,
        from: { name: user.name, imageSrc: user.imageSrc, userId: user._id },
        to: {
          teamId: data.to.teamId,
          userId: data.from.userId,
        },
      });
    });

    anotherPeer.current.on('stream', (stream) => {
      partnerVideo.current.srcObject = stream;
    });

    anotherPeer.current.on('close', () => {});

    if (!callEstablished) {
      callEstablished = true;
      anotherPeer.current.signal(data.signalFromCaller);
    }

    socket.on('call interrupted', (data) => {
      window.location.reload();
    });
  };

  const tryCall = async () => {
    setCalling(true);
    await fetchStreamFromMediaDevices(false);

    const peer = new Peer({
      initiator: true,
      trickle: false,
      stream: localStream.current,
      config: iceConfig,
    });

    yourPeer.current = peer;

    peer.on('signal', (signal) => {
      if (!callEstablished) {
        callEstablished = true;
        socket.emit('try call', {
          signalFromCaller: signal,
          from: { name: user.name, imageSrc: user.imageSrc, userId: user._id },
          to: {
            teamId: p2p.subscriber.teamId,
            userId: p2p.subscriber.whom._id,
          },
        });
      }
    });

    peer.on('stream', (stream) => {
      partnerVideo.current.srcObject = stream;
    });

    socket.on('call accepted', (data) => {
      peer.signal(data.signalFromPartner);
    });

    peer.on('close', () => {
      handleCallDown();
    });
    socket.on('call interrupted', (data) => {
      window.location.reload();
    });
  };

  const handleVideoSwitch = () => {
    localStream.current.getVideoTracks()[0].enabled = !localStream.current.getVideoTracks()[0].enabled;
    setLocalVideoEnabled(!localVideoEnabled);
  };

  const handleAudioSwitch = () => {
    localStream.current.getAudioTracks()[0].enabled = !localStream.current.getAudioTracks()[0].enabled;
    setLocalAudioEnabled(!localAudioEnabled);
  };

  useEffect(() => {
    setCalling(props.p2p.tryCall);
    setSubscriber(props.p2p.subscriber);

    if (props.p2p.tryCall) {
      tryCall(props.p2p.subscriber);
    }
  }, [props.p2p.tryCall]);

  useEffect(() => {
    if (p2p.callFrom) {
      ringtoneAudio.current.muted = false;
      setIncomingCall(true);
      ringtoneAudio.current.src = 'https://dev-parties.herokuapp.com/audio/rington1.mp3';
      ringtoneAudio.current.play();
    }
  }, [p2p.callFrom]);

  const handleCallDown = () => {
    socket.emit('put call down', { to: { teamId } });

    ringtoneAudio.current.muted = true;
    ringtoneAudio.current.src = '';
    setIncomingCall(false);
    props.dispatch(callRecieved(null));
  };

  return (
    <>
      <audio muted={true} ref={ringtoneAudio} src=""></audio>

      <div className={`incoming-call ${incomingCall ? 'incoming-show' : 'incoming-hide'}`}>
        <div className="incoming-user-img" style={{ backgroundUrl: p2p.callFrom?.imageSrc }}>
          {p2p.callFrom?.name.substr(0, 2)}
        </div>
        <div className="incoming-user-name">{p2p.callFrom?.name}</div>
        <div className="incoming-call-buttons">
          <div
            onClick={() => acceptCall(false)}
            style={{ background: '#42AF2C' }}
            className="micro-switch-btn call-btn"
          >
            <FontAwesomeIcon icon={faMicrophone} />
          </div>
          <div onClick={() => acceptCall(true)} style={{ background: '#42AF2C' }} className="video-switch-btn call-btn">
            <FontAwesomeIcon icon={faVideo} />
          </div>
          <div
            onClick={() => handleCallDown()}
            style={{ background: '#DF3A3A', transform: 'rotate(-138deg)' }}
            className="end-call-btn call-btn"
          >
            <FontAwesomeIcon icon={faPhone} />
          </div>
        </div>
      </div>
      <div className={`call-container ${calling && 'show-mini'}`}>
        {/* {props.p2p.tryCall && <span>Calling to {subscriber?.whom.name}</span>} */}
        <video id="localVideo" ref={localVideo} autoPlay muted />
        <video id="partnerVideo" ref={partnerVideo} autoPlay />

        <div className="call-buttons">
          <div
            style={{ background: localAudioEnabled ? '#42AF2C' : '#DF3A3A' }}
            onClick={handleAudioSwitch}
            className="micro-switch-btn call-btn"
          >
            <FontAwesomeIcon icon={localAudioEnabled ? faMicrophone : faMicrophoneSlash} />
          </div>
          <div
            style={{ background: localVideoEnabled ? '#42AF2C' : '#DF3A3A' }}
            onClick={handleVideoSwitch}
            className="video-switch-btn call-btn"
          >
            <FontAwesomeIcon icon={localVideoEnabled ? faVideo : faVideoSlash} />
          </div>
          <div
            onClick={() => handleCallDown()}
            style={{ background: '#DF3A3A', transform: 'rotate(-138deg)' }}
            className="end-call-btn call-btn"
          >
            <FontAwesomeIcon icon={faPhone} />
          </div>
        </div>
      </div>
    </>
  );
}

export default connect((state) => ({ p2p: state.p2p, socket: state.socket.socket, user: state.user.userData }))(Call);
