import React, { useContext } from 'react';
import { ThemeContext } from '../../core/Page/Page';
import './ThemedField.css';

function ThemedField(props) {
  const { theme } = useContext(ThemeContext);

  return (
    <div {...props} className={`form-field form-field-${theme}`}>
      {props.children}
    </div>
  );
}

export default ThemedField;
