import React, { useContext, useState } from 'react';
import { ThemeContext } from '../../core/Page/Page';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import colors from '../../colors'

import './TopicsInputField.css';


function TopicsInputField(props) {
  const { theme } = useContext(ThemeContext);
  const [topics, _setTopics] = useState([]);
  const SPACE_CODE = 32;
  const BACKSPACE_CODE = 8;

  const setTopics = (tpc) => {
    if (tpc.length <= props.max) {
      _setTopics(tpc);
      props.onTopicsChange(tpc);
    }
  };

  const handleBlur = (e) => {
    let {value} = e.target;

    if (value.trim()) {
      setTopics([...topics, value])
      e.target.value = ''
    }
  }

  const handleChange = (e) => {
    const value = e.target.value.trim();

    if (e.keyCode === SPACE_CODE) {
      value && setTopics([...topics, value]);
      e.target.value = '';
    } else if (!value && e.keyCode === BACKSPACE_CODE) {
      setTopics([...topics.splice(0, topics.length - 1)]);
    }
  };

  const handleTopicDelete = (topic) => {
    setTopics(topics.filter((t) => t != topic));
  };

  const mapTopicsToList = () => {
    return topics.map((topic) => {
      return (
        <li style={{background: colors.common.primary}} key={Math.random() * 10} className="topic">
          <span>{topic}</span>
          <FontAwesomeIcon onClick={() => handleTopicDelete(topic)} className="icon" icon={faTimes} />
        </li>
      );
    });
  };

  return (
    <>
      {props.children}
      <div className={`topics-input topics-input-${theme}`}>
        <div className="topics">
          <ul className="topics-list">{mapTopicsToList()}</ul>
        </div>
        <input onBlur={handleBlur} onKeyDown={handleChange} type="text" className="topics-input-field" />
      </div>
    </>
  );
}

export default TopicsInputField;
