import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import serverConfig from '../../server-config';
import ProfileView from './ProfileView';

function Profile({ match, userId, token }, props) {
  const [user, setUserData] = useState();
  const [isOwner, setIsOwner] = useState(false);

  useEffect(() => {
    fetchUser();
  }, [match, userId]);

  const fetchUser = async () => {
    const response = await fetch(`${serverConfig.baseUrl}/users/get/by/id/${match.params.userId}`, {
      headers: { 'Content-Type': 'application/json', Accept: 'application/json' },
    });

    const userData = await response.json();
    setUserData(userData);
    setIsOwner(userData._id === userId);
  };

  return user ? <ProfileView token={token} owner={isOwner} user={user} /> : <p>loading...</p>;
}

export default connect((state) => ({
  userId: state.auth.userId,
  confirmed: state.user.userData.isConfirmed,
  token: state.auth.token,
}))(Profile);
