import React, { useState, useEffect } from 'react';
import LoginForm from './LoginForm';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { Redirect } from 'react-router-dom';

import { requestLogin } from '../../actions/auth';

const FormError = styled.div`
  color: rgb(204, 30, 30);
  font-weight: bold;
  font-size: 1.2rem;
  margin-bottom: 20px;
`;

function Login(props) {
  const [redirect, setRedirect] = useState(false)

  useEffect(() => {
    
  }, [props])

  const loginError = props.errorText;

  const handleSubmit = async ({ email, password }) => {
    props.dispatch(requestLogin(email, password));
  };

  // if (redirect) {
  //   return <Redirect to="/" />
  // }

  // when user authorized userId will not be empty
  return (
    <>
      <LoginForm onSubmit={handleSubmit}>{loginError && <FormError>{loginError}</FormError>}</LoginForm>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
  errorText: state.auth.errorText,
  userId: state.auth.userId,
}))(Login);
