import React, { createContext, useState, useEffect } from 'react';
import './Page.css';

export const ThemeContext = createContext('dark');

function Page({ children }) {
  const [theme, setTheme] = useState('dark');

  useEffect(() => {
    const savedTheme = localStorage.getItem('dev-parties-theme') || 'dark';
    setTheme(savedTheme);
  }, []);

  useEffect(() => {
    localStorage.setItem('dev-parties-theme', theme);
  }, [theme]);

  const toggleTheme = () => {
    const savedTheme = localStorage.getItem('dev-parties-theme');
    setTheme(savedTheme === 'light' ? 'dark' : 'light');
  };

  return (
    <ThemeContext.Provider value={{ theme, toggleTheme }}>
      <div className={`app-page app-page-${theme}`}>{children}</div>
    </ThemeContext.Provider>
  );
}

export default Page;
