import React, { useEffect, useState } from 'react';
import serverConfig from '../../../server-config';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUsers } from '@fortawesome/free-solid-svg-icons';
import './TeamPublicView.css';

function TeamPubclicView(props) {
  const { teamId } = props.match.params;
  const [team, setTeamInfo] = useState(false);

  const fetchTeamInfo = async () => {
    const response = await fetch(`${serverConfig.baseUrl}/teams/get/${teamId}/public`);
    const json = await response.json();

    setTeamInfo(json);
    console.log(json)
  };

  useEffect(() => {
    fetchTeamInfo();
  }, []);

  const mapTechnologies = (team) => {
    if (team.technologies && team.technologies.length > 0) {
      return team.technologies.map((tech, index) => {
        return <li key={index}>{tech}</li>
      })
    }
  }

  return team ? (
    <div className="team-main-container">
      <div className="team-img" style={{ backgroundImage: `url(${serverConfig.baseUrl}/${team.imageSrc})` }}></div>
      <h1 className="team-name">{team.name}</h1>
      <div className="techonologies">
        <ul>{mapTechnologies(team)}</ul>
      </div>
      <h3 className="team-description">{team.description}</h3>
      <h3 className="team-teammates">
        <FontAwesomeIcon icon={faUsers} />
        <span>{team.teammates}</span>
      </h3>
    </div>
  ) : (
    <p>loading...</p>
  );
}

export default TeamPubclicView;
