import React, { useContext, useState, useRef, useEffect } from 'react';
import ThemedField from '../../../shared/ThemedField/ThemedField';
import colors from '../../../colors';
import { ThemeContext } from '../../../core/Page/Page';
import serverConfig from '../../../server-config';
import { distinctUntilChanged, debounceTime, map } from 'rxjs/operators';
import { fromEvent } from 'rxjs';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';

import './TeamSettings.css';
import { Link } from 'react-router-dom';

function TeamSettings(props) {
  const { team } = props;
  const { theme } = useContext(ThemeContext);
  const [searchedUsers, setSearchedUsers] = useState([]);
  const userSearchInput = useRef();

  const handleUserSearch = async (val) => {
    if (val.length < 3) {
      setSearchedUsers([]);
      return;
    }

    const data = val.split(' ').join('+');

    const response = await fetch(`${serverConfig.baseUrl}/users/search/by/name/${data}`);
    const json = await response.json();

    const filtered = json.filter((user) => {
      const teammatesIds = team.teammates.map((t) => t._id);
      return !teammatesIds.includes(user._id) && user.emailConfirmed;
    });

    const users = filtered.map((u) => {
      if (team.invitations.map((inv) => inv.userId).includes(u._id)) {
        return { ...u, invited: true };
      }

      return { ...u, invited: false };
    });

    setSearchedUsers(users);
  };

  const handleUserInvitation = async (action, userId) => {
    userSearchInput.current.value = '';

    await fetch(`${serverConfig.baseUrl}/teams/${action}/${team._id}/${userId}`, {
      headers: {
        Authorization: `Bearer ${props.token}`,
      },
    });

    setSearchedUsers([]);
    props.onTeamChanged();
  };

  const handleUserInviteClick = async (userId) => {
    handleUserInvitation('invite', userId);
  };

  const handleInvitationWithdrawClick = async (userId) => {
    handleUserInvitation('withdraw', userId);
  };

  const handleParticipantReject = async (userId) => {
    const confirm = window.confirm('Are you sure you want to reject this participant?');

    if (confirm) {
      await fetch(`${serverConfig.baseUrl}/teams/update/${team._id}/teammates/kick/${userId}`, {
        method: 'put',
        headers: {
          Authorization: `Bearer ${props.token}`,
        },
      });
    }
  };

  useEffect(() => {
    const event = fromEvent(userSearchInput.current, 'input').pipe(
      map((e) => e.target.value),
      debounceTime(500),
      distinctUntilChanged()
    );
    const subscription = event.subscribe((val) => handleUserSearch(val));
    return () => subscription.unsubscribe();
  });

  const editableTeammates = () => {
    return team.teammates.map((teammate) => {
      return (
        <li key={teammate._id}>
          <EditableTeammate
            key={teammate._id}
            onReject={handleParticipantReject}
            user={props.user}
            teammate={teammate}
          />
        </li>
      );
    });
  };

  const mapSearchResult = () => {
    return searchedUsers.map((user) => {
      return (
        <li className="user-mini" key={user._id}>
          <div
            className="user-mini-img"
            style={{ backgroundImage: `url(${serverConfig.baseUrl}/${user.imageSrc})` }}
          ></div>
          <Link target="_blank" to={`/profile/${user._id}`}>
            {user.name}
          </Link>
          <span className="user-mini-uuid" style={{ color: theme === 'dark' ? '#43527B' : '#b2b2b2' }}>
            #{user.uuid}
          </span>

          {user.invited ? (
            <>
              <button
                disabled={true}
                onClick={() => handleUserInviteClick(user._id)}
                className="disabled user-invite-btn"
              >
                Pending
              </button>

              <button onClick={() => handleInvitationWithdrawClick(user._id)} className="user-withdraw-btn">
                Withdraw
              </button>
            </>
          ) : (
            <button onClick={() => handleUserInviteClick(user._id)} className="user-invite-btn">
              Invite
            </button>
          )}
        </li>
      );
    });
  };

  const mapAlreadyInvitedUsers = () => {
    return team.invitations.map((user) => {
      return (
        <li className="user-mini" key={user.userId}>
          <div
            className="user-mini-img"
            style={{ backgroundImage: `url(${serverConfig.baseUrl}/${user.imageSrc})` }}
          ></div>
          <Link style={{ marginRight: '15px' }} target="_blank" to={`/profile/${user._id}`}>
            {user.name}
          </Link>
          <button onClick={() => handleInvitationWithdrawClick(user.userId)} className="user-withdraw-btn">
            Withdraw
          </button>
        </li>
      );
    });
  };

  return (
    <div className="team-settings-container">
      <h1 className="team-name-header">{team.name}</h1>
      <div className="teammates-container">
        <ul className="teammates-list">{editableTeammates()}</ul>
      </div>

      <div className="invite-teammate-container" style={{ borderColor: colors[theme].foreground }}>
        <h2 className="secondary-settings-header">Already invited users</h2>
        <ul className="search-result-list">{mapAlreadyInvitedUsers()}</ul>
      </div>

      <div className="invite-teammate-container" style={{ borderColor: colors[theme].foreground }}>
        <h2 className="secondary-settings-header">Invite participants</h2>
        <ThemedField>
          <label>Global users search</label>
          <input ref={userSearchInput} type="text" placeholder="type name or uid here..." />
        </ThemedField>
        <ul className="search-result-list">{mapSearchResult()}</ul>
      </div>
    </div>
  );
}

function EditableTeammate({ teammate, user, onReject }) {
  const { theme } = useContext(ThemeContext);
  console.log(teammate);

  return (
    <div className="user-mini" key={teammate._id}>
      <div
        className="user-mini-img"
        style={{
          borderColor: colors[theme].foreground,
          backgroundImage: `url(${serverConfig.baseUrl}/${teammate.imageSrc})`,
        }}
      >
        {!teammate.imageSrc && <span>{teammate.name.substr(0, 2)}</span>}
      </div>
      <Link
        style={{ color: colors[theme].foreground, marginRight: '5px' }}
        target="_blank"
        to={`/profile/${teammate._id}`}
      >
        {teammate.name}
      </Link>
      <ul className="roles-list">
        {teammate.roles.map((role) => {
          return (
            <li key={role}>
              <div style={{ backgroundColor: role.toLowerCase() === 'owner' && 'red' }} className="role">
                {role}
                <span style={{ marginLeft: '10px' }}>
                  <FontAwesomeIcon className="icon" icon={faTimes} />
                </span>
              </div>
            </li>
          );
        })}
      </ul>
      {teammate._id !== user._id && (
        <span onClick={() => onReject(teammate._id)} style={{ backgroundColor: 'red' }} className="role-handler-btn">
          Reject Participant
        </span>
      )}
      <span style={{ backgroundColor: colors.common.primary }} className="role-handler-btn">
        + Add role
      </span>
    </div>
  );
}

export default TeamSettings;
