import React, { useState, useRef } from 'react';
import { Form, Field, ErrorMessage, Formik } from 'formik';
import ThemedField from '../../shared/ThemedField/ThemedField';
import { FormTitle } from '../Register/RegisterForm';
import Emoji from '../../shared/Emoji';
import FileInput from '../../shared/FileInput/FileInput';
import TopicsInputField from '../../shared/TopicsInputField/TopicsInputField';
import styled from 'styled-components';
import Button from '../../shared/Button/Button'

import './CreateTeamForm.css';

const FieldsContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 700px;
  margin-bottom: 50px;
`;

function CreateTeamForm(props) {
  const [technologies, setTechnologies] = useState([]);
  const imageFormData = useRef()

  const handleFileAttached = (formData) => {
    imageFormData.current = formData;
  };

  const handleTechnologiesChange = (techs) => {
    setTechnologies(techs);
  };

  const handleSubmit = (values) => {
    props.onSubmit({...values, technologies, imageFormData: imageFormData.current})
  }

  return (
    <div className="app-create-team-form">
      <Formik
        initialValues={{ name: '', description: '', technologies: '' }}
        validate={(values) => {
          const errors = {};

          if (!values.name) {
            errors.name = 'Team name is required';
          }

          return errors;
        }}
        onSubmit={handleSubmit}
      >
        {() => (
          <Form className={`app-form app-create-form`}>
            <FileInput options={['discard']} onFileAttached={handleFileAttached} />

            <FormTitle style={{ fontSize: '4rem' }}>
              <span>Collaborate with friends</span>
              <Emoji label="first-left">ðŸ¤œ</Emoji>
              <Emoji label="first-right">ðŸ¤›</Emoji>
            </FormTitle>

            <FormTitle style={{ fontSize: '2rem', marginBottom: '60px' }}>
              <span>Create your own team!</span>
            </FormTitle>

            <FieldsContainer>
              <ThemedField>
                <label>Team name *</label>
                <Field placeholder="Amazingteam1337" type="text" name="name" />
                <ErrorMessage name="name" component="span" />
              </ThemedField>

              <ThemedField>
                <label>Team summary</label>
                <Field placeholder="Our amazing team is gonna do..." type="text" name="description" />
                <ErrorMessage name="description" component="span" />
              </ThemedField>

              <TopicsInputField max="6" onTopicsChange={handleTechnologiesChange}>
                <label>Technologes (such as node.js, react etc.) max 6</label>
              </TopicsInputField>
            </FieldsContainer>

            <Button type="submit">Create team</Button>
          </Form>
        )}
      </Formik>
    </div>
  );
}

export default CreateTeamForm;
