import React, { useContext, useState, useEffect } from 'react';
import { Redirect, Link } from 'react-router-dom';
import { ThemeContext } from '../Page/Page';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch, faUsers, faSun, faMoon, faCog, faUser, faSignOutAlt } from '@fortawesome/free-solid-svg-icons';
import ThemeSwitcherButton from './ThemeSwitcherButton';
import { connect } from 'react-redux';
import { signOut } from '../../actions/auth';

import colors from '../../colors';

import './Header.css';

function Header(props) {
  const { theme, toggleTheme } = useContext(ThemeContext);
  const [menuState, setMenuState] = useState('closed');
  const [redirect, setRedirect] = useState('');
  const [userData, setSignedUserData] = useState({ name: '', id: '' });

  useEffect(() => {
    setSignedUserData({ name: props.name, id: props.id });
  }, [props]);

  const toggleMenuState = () => {
    menuState === 'open' ? setMenuState('closed') : setMenuState('open');
  };

  const themeIcon = () => {
    return theme === 'light' ? <FontAwesomeIcon icon={faMoon} /> : <FontAwesomeIcon icon={faSun} />;
  };

  const handleMenuClick = () => {
    toggleMenuState();
  };

  const handleMenuItemClick = (url) => {
    setRedirect(url);
  };

  const handleSignOut = () => {
    props.dispatch(signOut());
  };

  return (
    <header style={{ backgroundColor: colors.common.primary }} className={`app-header app-header-${theme}`}>
      {redirect && <Redirect to={redirect} />}

      <div className="header-left-side">
        <h1 style={{ cursor: 'pointer' }} onClick={() => handleMenuItemClick('/')}>
          DevParties
        </h1>
      </div>
      <div className="header-right-side">
        <span>
          <Link to="/search">
            <FontAwesomeIcon icon={faSearch} /> <span style={{ margin: '0 10px' }}>Global search</span>
          </Link>
        </span>
        {!props.name && (
          <div className="header-unauth-links">
            <span>
              <Link to="/login">Login</Link>
            </span>
            <span>
              <Link to="/register">Sign Up</Link>
            </span>
          </div>
        )}

        <ThemeSwitcherButton onClick={toggleTheme}>{themeIcon()}</ThemeSwitcherButton>
        {props.name && (
          <div onClick={handleMenuClick} className="profile-header-settings">
            <a style={{ color: '#fff' }}>{userData.name}</a>
            <ul
              className={`profile-menu ${menuState}`}
              style={{ boxShadow: `0 5px 10px ${theme === 'dark' ? '#000' : '#ccc'}` }}
            >
              <li className="profile-menu-item">
                <Link
                  style={{ color: colors[theme].foreground, backgroundColor: colors[theme].body }}
                  to={`/profile/${userData.id}`}
                >
                  <FontAwesomeIcon icon={faUser} />
                  My Account
                </Link>
              </li>
              <li className="profile-menu-item">
                <Link style={{ color: colors[theme].foreground, backgroundColor: colors[theme].body }} to={`/settings`}>
                  <FontAwesomeIcon icon={faCog} />
                  Settings
                </Link>
              </li>
              <li className="profile-menu-item">
                <Link
                  to="/"
                  style={{ color: colors[theme].foreground, backgroundColor: colors[theme].body }}
                  onClick={handleSignOut}
                >
                  <FontAwesomeIcon icon={faSignOutAlt} />
                  Sign out
                </Link>
              </li>
            </ul>
          </div>
        )}
      </div>
    </header>
  );
}

export default connect((state) => ({
  name: state.user.userData.name,
  id: state.user.userData._id,
}))(Header);
