import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { HashRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import Home from './core/Home/Home';
import Register from './core/Register/Register';
import Page from './core/Page/Page';
import Header from './core/Header/Header';
import EmailSendedAlert from './core/EmailSendedAlert';
import Login from './core/Login/Login';
import Profile from './core/Profile/Profile';
import Settings from './core/Settings/Settings';
import CreateTeam from './core/Teams/CreateTeam';
import TeamContainer from './core/Teams/Team/TeamContainer';
import TeamSettingsContainer from './core/Teams/TeamSettings/TeamSettingsContainer';
import TeamPublicView from './core/Teams/TeamPublicView/TeamPublicView';
import GlobalSearch from './core/GlobalSearch/GlobalSearch';

import Call from './core/Call/Call';

import { refreshAccessToken } from './actions/auth';
import ReconfirmEmail from './core/ReconfirmEmail';

function App({ dispatch, signedIn }) {
  useEffect(() => {
    dispatch(refreshAccessToken());
  }, []);

  return (
    <Router>
      <Page>
        <Header />
        <Switch>
          <Route path="/profile/:userId" component={Profile} />
          <Route path="/search" component={GlobalSearch} />

          <Route path="/confirm/" signedIn={signedIn} component={EmailSendedAlert} />
          <GuardedRoute path="/settings/" signedIn={signedIn} component={Settings} />
          <GuardedRoute path="/teams/create" signedIn={signedIn} component={CreateTeam} />
          <GuardedRoute path="/teams/thread/:teamId" exact={true} component={TeamContainer} signedIn={signedIn} />
          <GuardedRoute path="/teams/thread/:teamId/settings" component={TeamSettingsContainer} signedIn={signedIn} />
          <GuardedRoute path="/re-confirm/" signedIn={signedIn} component={ReconfirmEmail} />

          <Route path="/" exact={true} component={Home} />
          <Route path="/login" component={Login} />
          <Route path="/register" component={Register} />
          <Route signedIn={signedIn} path="/team/:teamId" component={TeamPublicView} />
        </Switch>

        <Call />
      </Page>
    </Router>
  );
}

const GuardedRoute = ({ component, signedIn, ...rest }) => (
  <Route {...rest} component={component}>
    {signedIn == 'false' && <Redirect to="/" />}
  </Route>
);

export default connect((state) => ({ signedIn: state.auth.signedIn }))(App);
