import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import serverConfig from '../../../server-config';
import TeamThread from './TeamThread/TeamThread';
import io from 'socket.io-client';

import { tryCall } from '../../../actions/p2p';
import { intializeSocket } from '../../../actions/socket';

function TeamContainer(props) {
  const [userIsTeammate, setUserIsTeammate] = useState(false);
  const [teamInfo, setTeamInfo] = useState(false);
  const [socket, setSocket] = useState(false);
  const [connected, setConnected] = useState(false);

  const { teamId } = props.match.params;

  const fetchTeamData = async () => {
    if (teamInfo || !props.auth) return;

    const response = await fetch(`${serverConfig.baseUrl}/teams/get/by/id/${teamId}`);
    const json = await response.json();

    setTeamInfo(json);

    const ids = json.teammates.map((teammate) => teammate.userId);
    const result = ids.some((id) => id === props.user.userData._id);

    setUserIsTeammate(result);
  };

  const handleCallTry = (whom) => {
    props.dispatch(tryCall(whom));
  };

  useEffect(() => {
    const payloadReady = props.auth.token && teamInfo;

    if (payloadReady && !connected) {
      const sock = io(serverConfig.baseUrl, {
        query: {
          token: props.auth.token,
          method: 'connect',
          teamId: teamInfo._id,
        },
      });

      setSocket(sock);
      setConnected(true);
      props.dispatch(intializeSocket(sock));
    }
  }, [props.auth.token, teamInfo]);

  useEffect(() => {
    fetchTeamData();
  }, [props.user.userData._id, props.auth]);

  return (
    teamInfo && (
      <TeamThread
        tryCall={(whom) => handleCallTry(whom)}
        user={props.user.userData}
        token={props.auth.token}
        teamInfo={teamInfo}
        socket={socket}
      />
    )
  );
}

export default connect((state) => ({ socket: state.socket, auth: state.auth, user: state.user, p2p: state.p2p }))(
  TeamContainer
);
