import React from 'react';
import colors from '../../colors';

import './Home.css';

function Home() {
  return (
    <div className="app-home">
      <img className="main-img" src="./images/team.svg" />
      <h1 className="home-h1">Welcome to our DevParties service!</h1>
      <h2 className="home-h2">Start collaboration with your team!</h2>

      <div className="steps">
        <div className="step">
          <h2>1.</h2>
          <img src="./images/login.svg" />
          <h2>Register your account</h2>
        </div>

        <div className="dots">
          <div style={{ backgroundColor: colors.common.primary }} className="dot"></div>
          <div style={{ backgroundColor: colors.common.primary }} className="dot"></div>
          <div style={{ backgroundColor: colors.common.primary }} className="dot"></div>
        </div>

        <div className="step">
          <h2>2.</h2>
          <img src="./images/create-team.svg" />
          <h2>Create a team</h2>
        </div>

        <div className="dots">
          <div style={{ backgroundColor: colors.common.primary }} className="dot"></div>
          <div style={{ backgroundColor: colors.common.primary }} className="dot"></div>
          <div style={{ backgroundColor: colors.common.primary }} className="dot"></div>
        </div>

        <div className="step">
          <h2>3.</h2>
          <img src="./images/collab.svg" />
          <h2>Start collaboration</h2>
        </div>
      </div>
    </div>
  );
}

export default Home;
