import React, { useState, useContext, useRef, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCamera } from '@fortawesome/free-solid-svg-icons';
import { ThemeContext } from '../../core/Page/Page';

import './FileInput.css';

function FileInput(props) {
  const [imageToUpload, setImageToUpload] = useState();
  const [save, setShowSave] = useState();
  const [discard, setShowDiscard] = useState();
  const [imageSrc, setImageSrc] = useState('');
  const [imageTouched, setImagedTouched] = useState(false);
  const fileInputRef = useRef();

  useEffect(() => {
    setImageSrc(props.defaultSrc || '');
  }, [props.defaultSrc]);

  useEffect(() => {
    setShowSave(props.options.indexOf('save') !== -1);
    setShowDiscard(props.options.indexOf('discard') !== -1);
  }, [props]);

  const { theme } = useContext(ThemeContext);

  const handleFileInputChange = (e) => {
    const file = e.target.files[0];
    const blob = new Blob([file]);
    const url = window.URL.createObjectURL(blob);

    setImageToUpload(file);
    setImageSrc(url);
    setImagedTouched(true);

    const formData = new FormData();
    formData.append('image', file);

    if (props.onFileAttached) props.onFileAttached(formData);
  };

  const handleChangePhotoClick = () => {
    fileInputRef.current.click();
  };

  const handleImageSave = async () => {
    const formData = new FormData();
    formData.append('image', imageToUpload);
    props.onFileSave(formData);

    setImagedTouched(false);
  };

  const handleImageDiscard = () => {
    setImageToUpload({});
    setImageSrc(props.defaultSrc || '');
    setImagedTouched(false);
  };

  return (
    <>
      <div
        onClick={handleChangePhotoClick}
        style={{ backgroundImage: `url(${imageSrc})` }}
        className={`profile-img profile-img-${theme}`}
      >
        <FontAwesomeIcon icon={faCamera} />
        <div className="expanded-div">
          <span>Change photo</span>
          <input
            onChange={handleFileInputChange}
            ref={fileInputRef}
            type="file"
            className="file-input"
            style={{ display: 'none' }}
          />
        </div>
      </div>
      {imageTouched && (
        <div className="photo-actions">
          {save && (
            <span onClick={handleImageSave} className="save">
              Save
            </span>
          )}
          {discard && (
            <span onClick={handleImageDiscard} className="discard">
              Discard
            </span>
          )}
        </div>
      )}
    </>
  );
}

export default FileInput;
