import React, { useEffect, useState } from 'react';
import TeamSettings from './TeamSettings';
import serverConfig from '../../../server-config';
import { connect } from 'react-redux';

function TeamSettingsContainer(props) {
  const { teamId } = props.match.params;
  const [teamInfo, setTeamInfo] = useState(false);
  const { user } = props;

  const fetchTeamInfo = async () => {
    const response = await fetch(`${serverConfig.baseUrl}/teams/get/by/id/${teamId}`);
    const teamJson = await response.json();

    if (user._id === teamJson.ownerId) {
      setTeamInfo(teamJson);
    }
  };

  useEffect(() => {
    if (user) {
      fetchTeamInfo();
    }
  }, [user]);

  return teamInfo && <TeamSettings onTeamChanged={fetchTeamInfo} user={user} token={props.token} team={teamInfo} />;
}

export default connect((state) => ({ token: state.auth.token, user: state.user.userData }))(TeamSettingsContainer);
