import React, { useContext, useRef, useState, useEffect } from 'react';
import { Formik, Form, ErrorMessage, Field } from 'formik';
import ThemedField from '../../shared/ThemedField/ThemedField';
import Button from '../../shared/Button/Button';
import { connect } from 'react-redux';
import { fetchUserData, uploadPhoto } from '../../actions/user';
import FileInput from '../../shared/FileInput/FileInput';
import serverConfig from '../../server-config';

import './Settings.css';

function Settings(props) {
  const { user } = props;
  const [userImageSrc, setUserImageSrc] = useState('');
  const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;

  useEffect(() => {
    setUserImageSrc(serverConfig.baseUrl + '/' + user.imageSrc);
  }, [props]);

  const handleFileSave = (formData) => {
    props.dispatch(uploadPhoto(user._id, formData, props.jwt));
  };

  const handleSubmit = async (values) => {
    await fetch(`${serverConfig.baseUrl}/users/update/properties/${user._id}`, {
      method: 'put',
      headers: {
        Authorization: `Bearer ${props.jwt}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(values),
    });

    props.dispatch(fetchUserData(user._id));
  };

  return (
    user && (
      <div className="app-profile-settings">
        <Formik
          initialValues={{
            name: user.name || '',
            bio: user.bio || '',
            github: user.github || '',
            linkedin: user.linkedin || '',
            facebook: user.facebook || '',
          }}
          validate={(values) => {
            const errors = {};

            if (values.github && !/^https?:\/\/w?w?w?\.?github\.com/.test(values.github)) {
              errors.github = 'Github link is not correct';
            }

            if (values.linkedin && !/^https?:\/\/w?w?w?\.?linkedin\.com/.test(values.linkedin)) {
              errors.linkedin = 'Linkedin link is not correct';
            }

            if (values.facebook && !/^https?:\/\/w?w?w?\.?facebook\.com/.test(values.facebook)) {
              errors.facebook = 'Facebook link is not correct';
            }

            if (!values.name) {
              errors.name = 'Name is required';
            }

            return errors;
          }}
          onSubmit={(values) => handleSubmit(values)}
        >
          {({ handleSubmit }) => (
            <Form onSubmit={handleSubmit} className="app-profile-settings-form ">
              <FileInput defaultSrc={userImageSrc} options={['save', 'discard']} onFileSave={handleFileSave} />

              <ThemedField>
                <label style={{ marginTop: '20px' }}>Fullname</label>
                <Field type="text" name="name" placeholder={`current name is ${user.name}`} />
                <ErrorMessage name="name" component="span" />
              </ThemedField>

              <ThemedField>
                <label>Tell others about yourself. Feel free...</label>
                <Field type="text" name="bio" placeholder="I am a web develop..." />
              </ThemedField>

              <ThemedField>
                <label>Github profile:</label>
                <Field type="text" name="github" placeholder="https://github.com/<your_username>" />
                <ErrorMessage name="github" component="span" />
              </ThemedField>

              <ThemedField>
                <label>Linkedin:</label>
                <Field type="text" name="linkedin" placeholder="https://linkedin.com/in//<your_username>" />
                <ErrorMessage name="linkedin" component="span" />
              </ThemedField>

              <ThemedField>
                <label>Facebook:</label>
                <Field type="text" name="facebook" placeholder="https://facebook.com/profile..." />
                <ErrorMessage name="facebook" component="span" />
              </ThemedField>
              {props.children}
              <Button type="submit">Save</Button>
            </Form>
          )}
        </Formik>
      </div>
    )
  );
}

export default connect((state) => ({ user: state.user.userData, jwt: state.auth.token }))(Settings);
