import React from 'react';
import { Redirect } from 'react-router-dom';

function EmailSendedAlert({ location }) {
  const { state } = location;

  if (!location.email) {
    return <Redirect path="home" />;
  }

  return (
    <div>
      <h1 style={{ fontSize: '7rem', margin: '40px' }}>
        Thank you for registration!
        <span aria-label="party-emoji" role="img">
          ðŸŽ‰
        </span>
      </h1>
      <h2 style={{ fontSize: '3rem', margin: '40px' }}>
        Confirmation link has been sent to <span style={{ textDecoration: 'underline' }}>{location.email}</span>
      </h2>
    </div>
  );
}

export default EmailSendedAlert;
