import React, { useContext, useRef, useEffect, useState } from 'react';
import './GlobalSearch.css';
import { ThemeContext } from '../Page/Page';
import { Link } from 'react-router-dom';
import { fromEvent } from 'rxjs';
import { distinctUntilChanged, debounceTime, map } from 'rxjs/operators';
import serverConfig from '../../server-config';

function GlobalSearch() {
  const { theme } = useContext(ThemeContext);
  const searchFieldRef = useRef();
  const [users, setUsers] = useState([]);

  useEffect(() => {
    const event = fromEvent(searchFieldRef.current, 'input').pipe(
      map((e) => e.target.value),
      debounceTime(500),
      distinctUntilChanged()
    );
    const subscription = event.subscribe((val) => handleSearchChange(val));
    return () => subscription.unsubscribe();
  });

  const handleSearchChange = async (val) => {
    const response = await fetch(`${serverConfig.baseUrl}/users/search/${val}`);
    const json = await response.json();

    setUsers(json);
  };

  const mapResults = () => {
    return users.map((user) => {
      return (
        <li key={user._id}>
          <UserPreview {...user} />
        </li>
      );
    });
  };

  return (
    <div className="main-search-container">
      <div className="search-bar">
        <h1>Find teammate by name or technology</h1>
        <input
          ref={searchFieldRef}
          placeholder="Steve Jo..."
          className={`search-field search-field-${theme}`}
          type="text"
        />
      </div>
      <div className="search-results">
        <ul>{mapResults()}</ul>
      </div>
    </div>
  );
}

function UserPreview({ name, _id, bio, imageSrc }) {
  const formatBio = (bio) => {
    return `${bio.substr(0, 200)}...`;
  };

  return (
    <Link style={{ margin: '30px' }} to={`/profile/${_id}`}>
      <div className="user-preview-container">
        <div style={{ backgroundImage: `url(${serverConfig.baseUrl}/${imageSrc})` }} className="user-image-wrap"></div>
        <div className="user-info">
          <h3>{name}</h3>
          <h4>{formatBio(bio)}</h4>
        </div>
      </div>
    </Link>
  );
}

export default GlobalSearch;
